.Backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 800;
  opacity: 1;
  transition-duration: .25s;
  transition-timing-function: var(--bezier-functiion);
  background: rgba(47, 48, 58, .4);
  position: fixed;
  top: 0;
  left: 0;
}

.Backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.Modal {
  width: 90%;
  transition: transform 250ms var(--bezier-functiion);
  background-color: #ffe550;
  border-radius: 30px;
  padding: 32px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

